import { differenceInDays } from "date-fns"
import { Stack, Typography } from "@mui/material"
import { useClerkPublicMetadata } from "@rp/react/common/hooks"
import { useAccount } from "@rp/react/common/providers"
import { useModal } from "@rp/react/web/common/providers"
import { Button } from "@rp/react/web/mui/core"
import { ChevronRight } from "@rp/react/web/mui/icons"

function TrialCTA() {
  const { quota } = useAccount()
  const difference = quota?.subscriptionRenewalDate
    ? differenceInDays(new Date(quota.subscriptionRenewalDate), new Date())
    : undefined

  const { data: publicMetadata } = useClerkPublicMetadata()
  const { self_registered: isSelfRegistered = false } = publicMetadata ?? {}

  const {
    planComparisonModal: { handleOpen: planComparisonHandleOpen },
  } = useModal()

  return (
    <Button
      onClick={() => {
        planComparisonHandleOpen("sidebar")
      }}
      variant="contained-light"
      sx={{ padding: "10px 5px" }}
    >
      {" "}
      <Stack gap="2px">
        <Typography variant="body1" fontSize="12px" textAlign="center" textTransform="none">
          Free Trial{" "}
          {isSelfRegistered && difference !== undefined ? `Â· ${difference} days left` : ""}
        </Typography>
        <Stack
          direction="row"
          gap="2px"
          alignItems="center"
          justifyContent="center"
          sx={theme => ({ color: theme.palette.primary.light })}
          ml={1}
        >
          <Typography
            variant="body1"
            fontSize="13px"
            fontWeight={700}
            textAlign="center"
            textTransform="none"
          >
            Upgrade to Pro
          </Typography>
          <ChevronRight />
        </Stack>
      </Stack>
    </Button>
  )
}

export { TrialCTA }
